import { useState, useEffect, useCallback } from "react";
import {
  useMoralis,
  useMoralisWeb3Api,
} from "react-moralis";
import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
import CardHeader from "@mui/material/CardHeader";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import {BuySpiceMix} from "./BuySpiceMix"
import { CookBiryani } from "./CookBiryani"
import { BoostBiryani } from "./BoostBiryani"
import {EatBiryani} from "./EatBiryani";
import { BiryaniPlates } from "./BiryaniPlates";
import Typography from "@mui/material/Typography";
import settings from "../contractSettings.json"

export function IncomeBox() {
  const contractAddress = settings.contract.address;
  
  const {
    Moralis,
    enableWeb3,
    isWeb3Enabled,
    account,
  } = useMoralis();

  
  const [contractBalance, setContractBalance] = useState(0);
  const [walletBalance, setWalletBalance] = useState(0);
  const Web3Api = useMoralisWeb3Api();
  
  const getContractBalance = useCallback(() => {
    const options = {
      chain: "bsc testnet",
      address: contractAddress,
    };

    Web3Api.account.getNativeBalance(options).then(({ balance }) => {
      setContractBalance(Moralis.Units.FromWei(balance, "ether"));
    });
  }, [contractAddress, Moralis.Units, Web3Api.account]);

  const getWalletBalance = useCallback(() => {
    const options = {
      chain: "bsc testnet",
      address: account,
    };

    Web3Api.account.getNativeBalance(options).then(({ balance }) => {
      setWalletBalance(Moralis.Units.FromWei(balance, "ether"));
    });
  }, [account, Moralis.Units, Web3Api.account]);

  useEffect(() => {
     (async() => {
      await enableWeb3();
    })();
  }, [enableWeb3]);

  useEffect(() => {
    if (isWeb3Enabled && account) {
      getContractBalance();
      getWalletBalance()
    }
  }, [isWeb3Enabled, account, getContractBalance, getWalletBalance]);

  return (
      <>
          <Grid container>
              <Grid item sm={12}>
                  <Box  >
                      <Card
                          sx={{ minWidth: 275, backgroundColor: "white", 'border-radius': '15px', }}
                      >
                          <Grid container spacing={2} >
                              <Grid item xs={12}>
                                  <CardHeader style={{
                                      'float': 'left',
                                  }}
                                      subheader={<Typography sx={{ color: 'rgb(109, 218, 252)', 'font-size': '22px !important', }}>Contract</Typography>}
                                  />
                                  <CardHeader style={{
                                      'float': 'right',
                                  }}
                                      subheader={<Typography sx={{ color: 'rgb(109, 218, 252)', 'font-size': '22px !important', }}>{`${Number(contractBalance).toFixed(3)} BNB`}</Typography>}
                                  />
                              </Grid>
                              <Grid item xs={12}>
                                  <CardHeader style={{
                                      'float': 'left',
                                  }}
                                      subheader={<Typography sx={{ color: 'rgb(37, 227, 132)', 'font-size': '22px !important', }}>Wallet</Typography>}
                                  />
                                  <CardHeader style={{
                                      'float': 'right',
                                  }}
                                      subheader={<Typography sx={{ color: 'rgb(37, 227, 132)', 'font-size': '22px !important', }}>{`${Number(walletBalance).toFixed(3)} BNB`} </Typography>}
                                  />
                              </Grid>
                              <Grid item xs={12}>
                                  <CardHeader style={{
                                      'float': 'left',
                                  }}
                                      subheader={<Typography sx={{ color: 'rgb(232, 202, 47)', 'font-size': '22px !important', }}>Your Plates </Typography>}
                                  />
                                  <CardHeader style={{
                                      'float': 'right',
                                  }}
                                      subheader={<Typography sx={{ color: 'rgb(232, 202, 47)', 'font-size': '22px !important', }}>{`${Number(walletBalance).toFixed(3)} `} </Typography>}
                                  />
                              </Grid>
                          </Grid>
                          <CardContent>
                              <BuySpiceMix />
                          </CardContent>
                          <Grid container spacing={2} >
                              <Grid item xs={12}>
                                  <CardHeader style={{
                                      'float': 'left',
                                  }}
                                      subheader={<Typography sx={{ color: 'rgb(109, 218, 252)', 'font-size': '22px !important', }}>Your Rewards</Typography>}
                                  />
                                  <CardHeader style={{
                                      'float': 'right',
                                  }}
                                      subheader={<Typography sx={{ color: 'rgb(109, 218, 252)', 'font-size': '22px !important', }}>{`${Number(contractBalance).toFixed(3)} BNB`} </Typography>}
                                  />
                              </Grid>
                          </Grid>
                          <CardActions>
                              <Grid container spacing={2} >
                                  <Grid item xs={4}>
                                      <CookBiryani />
                                  </Grid>
                                  <Grid item xs={4}>
                                      <BoostBiryani />
                                  </Grid>
                                  <Grid item xs={4}>
                                      <EatBiryani />
                                  </Grid>
                              </Grid>
                          </CardActions>
                      </Card>
                  </Box>
              </Grid>
          </Grid>

         
        
    </>
  );
}
