import Button from "@mui/material/Button";
import { useNotification } from "web3uikit";
import { useMoralis, useWeb3ExecuteFunction } from "react-moralis";

import settings from "../contractSettings.json";
import eatBiryaniAbi from "../abi/sellEggs.json";

export function EatBiryani() {
  const {  account } = useMoralis();
  const contractProcessor = useWeb3ExecuteFunction();
  const dispatch = useNotification();

  const eatBiryaniService = async () => {
    const contractAddress = settings.contract.address;

    let options = {
      contractAddress: contractAddress,
      functionName: eatBiryaniAbi.name,
      abi: [eatBiryaniAbi],
      params: {
        ref: account,
      }
    };

    await contractProcessor.fetch({
      params: options,
      onSuccess: () => {
        dispatch({
          type: "success",
          message: `Nice! You are going to set a new message!!`,
          title: "Message set",
          position: "topL",
        });
      },
      onError: (err) => {
        dispatch({
          type: "error",
          message: `${err.data.message}`,
          title: "Cook Biryani",
          position: "topL",
        });
      },
    });
  };

  return ( 

      <Button size="medium" style={{
          color: 'white',
          borderRadius: 10,
          height: 48,
          'display': 'flex',
          'margin-left': '2%',
          width: '100%',
          'letter-spacing': '3px',
      }} onClick={eatBiryaniService}>
          Eat<br/>Biryani
    </Button>
  );
}
