import Button from "@mui/material/Button";
import { useNotification } from "web3uikit";
import { useMoralis, useWeb3ExecuteFunction } from "react-moralis";

import settings from "../contractSettings.json";
import cookBiryaniABI from "../abi/hatchEggs.json";

export function CookBiryani() {
  const { account } = useMoralis();
  const contractProcessor = useWeb3ExecuteFunction();
  const dispatch = useNotification();

  const cookBiryaniService = async () => {
    const contractAddress = settings.contract.address;

    let options = {
      contractAddress: contractAddress,
      functionName: cookBiryaniABI.name,
      abi: [cookBiryaniABI],
      params: {
        ref: account,
      },
    };

    await contractProcessor.fetch({
      params: options,
      onSuccess: () => {
        dispatch({
          type: "success",
          message: `Nice! You are going to set a new message!!`,
          title: "Message set",
          position: "topL",
        });
      },
      onError: (err) => {
        dispatch({
          type: "error",
          message: `${err.data.message}`,
          title: "Cook Biryani",
          position: "topL",
        });
      },
    });
  };

  return (
      <Button style={{

          borderRadius: 10,
          color: 'white',
          height: 48,
          'display': 'flex',
          'margin-right': '2%',
          width: '100%',
          'letter-spacing': '3px',

      }} onClick={cookBiryaniService}>
        Cook Biryani
    </Button>
  );
}
