import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import { Input, useNotification } from "web3uikit";
import { useState } from "react";
import { useMoralis, useWeb3ExecuteFunction } from "react-moralis";
import Box from "@mui/material/Box";
import settings from "../contractSettings.json";
import buySpiceMixAbi from "../abi/buyEggs.json";


export function BuySpiceMix() {

    const [bnb, setBnb] = useState(0);
    const { Moralis, account } = useMoralis();
    const contractProcessor = useWeb3ExecuteFunction();
    const dispatch = useNotification();

  const buySpiceMixService = async () => {
    const contractAddress = settings.contract.address;

    let options = {
      contractAddress: contractAddress,
      functionName: buySpiceMixAbi.name,
      abi: [buySpiceMixAbi],
      params: {
        ref: account,
      },
      msgValue: Moralis.Units.ETH("" + bnb),
    };

    await contractProcessor.fetch({
      params: options,
      onSuccess: () => {
        dispatch({
          type: "success",
          message: `Nice! You are going to set a new message!!`,
          title: "Message set",
          position: "topL",
        });
      },
      onError: (err) => {
        dispatch({
          type: "error",
          message: `${err.data.message}`,
          title: "Cook Biryani",
          position: "topL",
        });
      },
    });
  };

  return (
    <>
          <Box>
              <Input style={{
                  background: '#dfe9f5',
                  outline: 'none',
                  border: 'none',
                  'margin-bottom': '10px',
                  'border-radius': '10px',
                  'width': '100% !important',
                  'padding': '12px 14px',
                  'display': 'block',
                  

              }}
                  type="number"
                  label=""
                  value="0"
                  onChange={(e) => setBnb(e.target.value)}
                  step="0.01"
              />
          </Box>

          <Box>

              <Button onClick={account && buySpiceMixService}
                  style={{
                      borderRadius: 10,
                      height: 48,
                      width: '100%',
                      'letter-spacing': '3px',
                  }}
                  variant="contained"
              >
                  Buy Ingredients
</Button>
          </Box>

    </>
  );
}

